import React, { PureComponent } from 'react';
import styled, { ThemeProvider } from 'styled-components';
import { Section, Button } from '../components';
import { NAMESPACE as SECTION, THEME_PROPERTIES as SECTION_THEME_PROPERTIES } from '../components/Section';
import * as SPACING from '../patterns/spacing';
import * as COLORS from '../patterns/colors';

const PositionnedSection = styled(Section)`
    ${SPACING.CONTAINER}
    padding-top: 60px;
    padding-bottom: 60px;
`;

const PositionnedButton = styled(Button)`
    margin: 20px auto;
`;

export default class ResumeSection extends PureComponent {

    get theme() {
        const { theme: descendantTheme = {} } = this.props;

        const theme = {
            [SECTION]: {
                [SECTION_THEME_PROPERTIES.BACKGROUND_COLOR]: COLORS.DARK_GREY,
                [SECTION_THEME_PROPERTIES.COLOR]: COLORS.WHITE,
            }
        }

        return inheritedTheme => (Object.assign(
            inheritedTheme,
            theme,
            descendantTheme
        ))
    }

    render() {
        const { ...props } = this.props;
        return (<ThemeProvider theme={this.theme}><PositionnedSection {...props}>
            Hello world!
            <PositionnedButton to=''>Télécharger mon CV</PositionnedButton>
        </PositionnedSection></ThemeProvider>);
    }
}